.button {
  width: 100%;
  padding: 6px 12px;
  border: 2px solid #000000;
  background: #FFFFFF;
  font-weight: bold;
  cursor: pointer; }
  .button.button-primary {
    border-color: green;
    color: green; }
    .button.button-primary:hover {
      background: green;
      color: #FFFFFF; }
  .button.button-danger {
    border-color: red;
    color: red; }
    .button.button-danger:hover {
      background: red;
      color: #FFFFFF; }
  .button:disabled {
    border-color: grey;
    color: grey; }
    .button:disabled:hover {
      background: #FFFFFF;
      color: grey;
      cursor: not-allowed; }
  .button:hover {
    background: #000000;
    color: #FFFFFF; }

@media (min-width: 768px) {
  .button {
    max-width: 12em; } }
