.layout {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  width: 100%;
  font-family: 'Roboto', sans-serif; }

.layout__navigation {
  width: 100%;
  background: #FFFFFF;
  position: relative; }

.layout__content {
  max-width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto; }

@media (min-width: 768px) {
  .layout__content {
    width: 50em; } }
